import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { InputGroupInline, isEmpty, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { CLASS_KEY, RelacaoEntradaSaidaRV } from '../relacaoEntradaSaida/relacaoEntradaSaidaRV';

const REPORT_EVENT = 'printRelacaoOpInterestaduais';
const REPORT_TITLE = 'Relação das Operações Interestaduais';

export default function OperacoesInterestaduaisPEDReportView(props: WindowComponentProps) {
  const history = useHistory();

  const formProps = useForm<RelacaoEntradaSaidaRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const basicData: PrinterDataBasic = {
    ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY),
  };

  useEffect(
    () => {
      if (!isEmpty(globalReducer.globalParameter.empresa)) {
        const id = globalReducer.globalParameter.empresa.id;
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', id);
          formProps.setValue('modelo', true);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportFormView formProps={formProps} printerData={basicData} history={history} extraConfigOpened={true}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <InputGroupInline formProps={formProps} id="modelo" hidden={true} />
      </ReportFormView>
    </WindowComponent>
  );
}
